.filmPage {
  width: 100%;
}

.notFoundTitle {
  width: 100%;
  margin: 70px 0;
  padding: 0 45px;
  font-size: 30px;
  text-align: center;
}

.filmMain,
.filmContent {
  padding: 50px 120px;
}

.filmMain {
  min-height: 400px;
  color: #ffffff;
  background-color: #303030;
}

.filmMainWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 1000px;
  margin: 0 auto;
}

.imageContainer {
  width: 300px;
  min-width: 300px;
  margin-right: 90px;

  img {
    max-width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 38px;
}

.rating {
  margin-bottom: 15px;
}

.descriptionText {
  margin-top: 0;
  margin-bottom: 15px;
  line-height: 1.5;
}

.filmContentWrapper {
  max-width: 1000px;
  margin: 0 auto;
}

