.form {
  display: flex;
  flex-direction: column;

  p {
    margin-top: 0;
    margin-bottom: 15px;
  }

  label {
    display: block;
  }
}

.title {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 20px;
}

.textInput {
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  color: #242424;
  border: 1px solid #242424;
  transition: all 0.3s ease;

  &:hover {
    border-color: rgb(36, 36, 36, 0.6);
  }
}

.submit {
  padding: 12px 25px;
  color: #ffffff;
  background-color: #296ef9;
  border: 2px solid #19408e;
  cursor: pointer;
  transition: opacity 0.3s ease;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.errorMessageList {
  margin-top: 20px;
  font-size: 14px;
  color: #e33618;
}

p.errorMessage {
  margin-top: 0;
  margin-bottom: 12px;
}
