.title {
  margin: 0;
  padding: 15px 20px;
  font-size: 18px;
}

.content {
  border-top: 2px solid #d6d6d6;
}

.line {
  display: grid;
  grid-template-columns: 2fr 3fr;
  margin: 0;
  padding: 15px 10px;
  border-bottom: 1px solid #d6d6d6;
}

.lineTitle {
  font-weight: 700;
}

.lineContent {
  margin-left: 0;
}

.genre {
  &:not(:last-of-type)::after {
    content: ", ";
  }
}
