*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  color: #1f2124;
  background-color: #ffffff;
}

/* stylelint-disable-next-line */
#root {
  flex-grow: 1;
  display: flex;
  justify-content: stretch;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-width: 320px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
