.notFoundTitle {
  margin-top: 20px;
  margin-bottom: 0;
  padding: 0 45px;
  font-size: 30px;
}

.filmPage {
  width: 100%;
  display: flex;
}

.imageContainer {
  width: 300px;
  min-width: 300px;
  margin-right: 90px;

  img {
    max-width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.description {
  padding-right: 100px;
}

.title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 38px;
}

.genre {
  &:not(:last-of-type)::after {
    content: ", ";
  }
}

.genresTitle {
  font-weight: 600;
}
