.pageWrapper {
  display: flex;
  align-items: flex-start;
  width: 100%;
  padding: 30px 45px;
}

.filmsTitle {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.filters {
  width: 252px;
  margin-right: 45px;
  padding: 30px 20px;
  border: 1px solid #dadada;
  border-radius: 4px;
  box-shadow: 4px 4px 8px 1px #c5c5c5;
}

.filtersTitle {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 26px;
}

.filmsItems {
  flex-grow: 1;
}
