.resetButton {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px 20px;
  text-align: center;
  color: #ffffff;
  background-color: #202c31;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: opacity 0.3s ease;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.sortingLabel {
  display: block;
  margin-bottom: 20px;

  p {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 14px;
  }

  select {
    width: 100%;
    padding: 8px 12px;
    background-color: inherit;
    border: 1px solid #dadada;
    border-radius: 4px;
    transition: all 0.3s ease;
    cursor: pointer;

    &:hover {
      border-color: #464646;
    }

    &:disabled {
      cursor: auto;
      opacity: 0.5;
    }

    &:disabled:hover {
      border-color: #dadada;
    }
  }
}

.genresGroup {
  padding-left: 0;
  border: none;

  label {
    display: block;
    margin-bottom: 10px;
    margin-left: -8px;
    cursor: pointer;
  }

  input[type="checkbox"] {
    cursor: pointer;

    :disabled {
      cursor: auto;
      opacity: 0.5;
    }
  }
}
