.label {
  display: block;
  margin-bottom: 30px;
  font-weight: 600;

  p {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 14px;
  }

  select {
    width: 100%;
    padding: 8px 12px;
    background-color: inherit;
    border: 1px solid #dadada;
    border-radius: 4px;
    transition: all 0.3s ease;
    cursor: pointer;

    &:hover {
      border-color: #464646;
    }

    &:disabled {
      cursor: auto;
      opacity: 0.5;
    }

    &:disabled:hover {
      border-color: #dadada;
    }
  }
}

.line {
  margin-bottom: 30px;
}

.button {
  display: block;
  width: 100%;
  padding: 8px 20px;
  text-align: center;
  font-size: 13px;
  color: #ffffff;
  letter-spacing: 0.03em;
  text-decoration: none;
  background-color: #296ef9;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background-color: #0445c9;
  }

  &:active {
    background-color: rgba(41, 110, 249, 0.5);
  }
}
