.app {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
}

.main {
  flex-grow: 1;
  display: flex;
  align-items: flex-start;
}

.mainTitle {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
