.outerWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title {
  margin-top: 0;
  margin-bottom: 15px;
  letter-spacing: 0.03em;
  font-weight: 600;
}

.firstLabel {
  margin-right: 15px;
  cursor: pointer;
}

.secondLabel {
  margin-left: 15px;
  cursor: pointer;
}

.innerWrapper {
  position: relative;
  display: flex;
  width: 40px;
  min-width: 40px;
  height: 20px;
  background-color: rgba(41, 110, 249, 0.5);
  border-radius: 12px;

  input[type="radio"] {
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    margin: 0;
    cursor: pointer;
    opacity: 0;
  }

  &::before {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 50%;
    content: "";
    background-color: #296ef9;
    border-radius: 50%;
    transition: transform 0.3s ease;
  }
}

.isActive {
  &::before {
    transform: translateX(-100%);
  }
}

.firstRadio {
  right: 50%;
  left: 0;
}

.secondRadio {
  right: 0;
  left: 50%;

  &:checked ~ .innerWrapper::before {
    transform: translateX(-100%);
  }
}
