.pageWrapper {
  display: flex;
  align-items: flex-start;
  width: 100%;
  padding: 30px 45px;
}

.filters {
  min-width: 300px;
  margin-right: 45px;
  padding: 30px 20px 50px;
  border: 1px solid #dadada;
  border-radius: 4px;
  font-size: 14px;
  box-shadow: 4px 4px 8px 1px #c5c5c5;
}

.filtersTitle {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 26px;
}

.filmDescription {
  flex-grow: 1;
}
