.modalContainer {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.modalWindow {
  position: fixed;
  z-index: 1100;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 460px;
  min-height: 300px;
  padding: 50px 45px;
  background-color: #ffffff;
  border-radius: 4px;
}

.closeButton {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;
  background-color: #296ef9;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  &::before,
  &::after {
    position: absolute;
    top: 6px;
    bottom: 6px;
    width: 2px;
    content: "";
    background-color: #ffffff;
    transform: translateX(-50%) rotate(45deg);
  }

  &::after {
    transform: translateX(-50%) rotate(-45deg);
  }

  &:hover {
    background-color: #19408e;
  }

  &:active {
    background-color: #4783fb;
  }
}
