.paginationButtons {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.paginationButton {
  flex-grow: 1;
  padding: 8px 20px;
  color: #ffffff;
  background-color: #296ef9;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background-color: #0445c9;
  }

  &:active {
    background-color: rgba(41, 110, 249, 0.5);
  }
}

.paginationPrev {
  margin-right: 10px;
  border-radius: 4px 0 0 4px;
}

.paginationNext {
  border-radius: 0 4px 4px 0;
}

.paginationPages {
  text-align: center;
}
