.header {
  display: flex;
  padding: 10px 45px;
  justify-content: space-between;
  align-items: center;
  background-color: #296ef9;
}

.navigation {
  display: flex;
  flex-wrap: wrap;
}

.navLink {
  position: relative;
  color: #ffffff;
  text-decoration: none;

  &::after {
    position: absolute;
    right: 0;
    bottom: -2px;
    left: 0;
    content: "";
    border-top: 1px solid #ffffff;
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &:hover {
    &::after {
      opacity: 1;
    }
  }

  &:active {
    opacity: 0.8;
  }
}

.loginButton {
  padding: 12px 20px;
  color: #eeeeee;
  letter-spacing: 0.03em;
  background-color: #449a43;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease;

  &:hover {
    background-color: #3a593a;
  }

  &:active {
    background-color: #46b944;
  }
}
