.filmCard {
  display: flex;
  width: 100%;
  min-width: 395px;
  border: 1px solid #dadada;
  border-radius: 4px;
  box-shadow: 4px 4px 8px 1px #c5c5c5;
}

.filmCover {
  position: relative;
  width: 90%;
  padding-bottom: 60.3%;
  background-color: rgba(41, 110, 249, 0.5);

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.filmInfo {
  width: 110%;
  display: flex;
  flex-direction: column;
}

.filmInfoTop {
  display: flex;
  align-items: center;
  padding: 18px;
}

.filmInfoRating {
  margin-right: auto;
}

.filmInfoFavourites,
.filmInfoBookmarks {
  max-width: 24px;
  max-height: 24px;
  margin-right: 16px;
  padding: 0;
  font-size: 26px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    svg path {
      fill: #edd824;
    }
  }

  &:active {
    transform: scale(0.9);
  }
}

.isAdded {
  svg path {
    fill: #edd824;
  }

  &:hover {
    opacity: 0.6;
  }
}

.filmInfoMain {
  padding: 18px;
}

.filmInfoTitle {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 18px;
}

.filmInfoBottom {
  margin-top: auto;
  padding: 20px 30px;
  border-top: 1px solid #c5c5c5;
}

.filmInfoLink {
  color: #222222;
  text-decoration: none;
  transition: color 0.3s ease;

  &:hover {
    color: #296ef9;
  }

  &:active {
    color: rgba(41, 110, 249, 0.8);
  }
}
